@tailwind base;
@tailwind components;
@tailwind utilities;

@media (max-width: 768px) {
  .key-feature-title {
    padding-bottom: 0.5rem; /* Reduce padding below title */
  }
  .key-feature-image {
    padding-bottom: 0.5rem; /* Minimal padding */
  }
  .key-feature-text {
    padding-top: 0.5rem; /* Minimal padding */
  }
  .key-feature-image-container {
    height: auto; /* Let the container height adjust to the image */
  }
  .key-feature-image {
    height: auto; /* Ensure the image height is not constrained */
  }
}

.key-features-wrapper {
  position: relative;
  z-index: 0;
}

.key-features-container {
  position: relative;
  z-index: 2;
  background-color: rgb(17 24 39);
}

.pulse-effect {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  z-index: 1;
  pointer-events: none;
  transform: translate(-50%, -50%);
}

.pulse-effect::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50vw; /* Clip to 50% of viewport width */
  height: 150%;
  background: radial-gradient(circle, rgba(147, 52, 233, 0.4) 20%, transparent 70%);
  border-radius: 50%;
  transform: translate(-50%, -50%) scale(0.5);
  animation: sonarPulse 2s infinite;
}

@keyframes sonarPulse {
  0% {
    transform: translate(-50%, -50%) scale(0.5);
    opacity: 0.9;
  }
  100% {
    transform: translate(-50%, -50%) scale(1.5);
    opacity: 0;
  }
}
